function initMap() {

    window.marker = null;

    function initialize() {
        var map = void 0;
        var marker = void 0;
        var marker2 = void 0;

        var moscow = new google.maps.LatLng(55.7522200, 37.6155600);
        var sherbinka = new google.maps.LatLng(55.49972, 37.55972);

        var style = [{
            "featureType": "road",
            "elementType": "labels.icon",
            "stylers": [{ "saturation": 1 }, { "gamma": 1 }, { "visibility": "on" }, { "hue": "#e6ff00" }]
        }, {
            "elementType": "geometry", "stylers": [{ "saturation": -100 }]
        }];

        var contentString = ' \n      <div class="bubble">\n      <div class="bubble-title">\u041C\u043E\u0441\u043A\u0432\u0430 </div>\n      <p class="text bubble-text">\u041A\u0440\u0430\u0441\u043D\u0430\u044F \u043F\u043B\u043E\u0449\u0430\u0434\u044C <br> 129226</p>\n      <p><a href="#" class="link link_red">info.juteksrussia@bintg.com</a></p>\n    </div>';

        var contentString2 = ' \n      <div class="bubble">\n      <div class="bubble-title">\u041C\u043E\u0441\u043A\u0432\u0430, \u0433. \u0429\u0435\u0440\u0431\u0438\u043D\u043A\u0430 </div>\n      <p class="text bubble-text">\u043F\u0440. \u041C\u0438\u0440\u0430, \u0432\u043B. 211,\u2028\u0422\u0420\u0426 "\u0417\u043E\u043B\u043E\u0442\u043E\u0439 \u0412\u0430\u0432\u0438\u043B\u043E\u043D", <br> 129226</p>\n      <p><a href="#" class="link link_red">info.juteksrussia@bintg.com</a></p>\n    </div>';

        var infowindow = new google.maps.InfoWindow({
            content: contentString
        });
        var infowindow2 = new google.maps.InfoWindow({
            content: contentString2
        });

        var mapOptions = {
            // SET THE CENTER
            center: moscow,

            // SET THE MAP STYLE & ZOOM LEVEL
            mapTypeId: google.maps.MapTypeId.ROADMAP,
            zoom: 10,

            // SET THE BACKGROUND COLOUR
            backgroundColor: "#eeeeee",

            // REMOVE ALL THE CONTROLS EXCEPT ZOOM
            panControl: false,
            zoomControl: true,
            mapTypeControl: false,
            scaleControl: false,
            streetViewControl: false,
            overviewMapControl: false,
            zoomControlOptions: {
                style: google.maps.ZoomControlStyle.SMALL
            }
        };

        map = new google.maps.Map(document.getElementById('map'), mapOptions);

        // SET THE MAP TYPE
        var mapType = new google.maps.StyledMapType(style, { name: "Grayscale" });
        map.mapTypes.set('grey', mapType);
        map.setMapTypeId('grey');

        //CREATE A CUSTOM PIN ICON
        var marker_image = 'images/icons/map-pin.svg';
        var pinIcon = new google.maps.MarkerImage(marker_image, null, null, null, new google.maps.Size(30, 30));

        marker = new google.maps.Marker({
            position: moscow,
            map: map,
            icon: pinIcon
        });

        marker2 = new google.maps.Marker({
            position: sherbinka,
            map: map,
            icon: pinIcon
        });

        marker2.setMap(map);

        marker.addListener('click', function () {
            infowindow2.close();
            infowindow.open(map, marker);
        });

        marker2.addListener('click', function () {
            infowindow.close();
            infowindow2.open(map, marker2);
        });
    }

    google.maps.event.addDomListener(window, 'load', initialize);
}
