      var tbodyTr = this.$tableBody.find('tr:not(.compare-table__title-row)');

      //fill image
      $(image).removeClass('_empty').find('.compare-table__image').append('<div class="compare-table__remove" data-id="' + index + '"></div>').removeClass('compare-table__image_empty').find('img').prop('src', info.img);

